.clear-button {
  border-radius: 32px;
  background-color: #041A55;

  font-family: "Inter", serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;

  height: 56px;
  width: 328px;
}

/* https://developers.id.me/brand-assets */
.idme-button {
  background-color: #08833D;
  font-family: "Open Sans Light", sans-serif;
}

.tools-button-icon {
  transition: transform 0.25s ease, opacity 0.25s ease;
}

.tools-button-icon-pop {
  animation: tools-button-pop 0.45s ease;
}

@keyframes tools-button-pop {
  0% {
    transform: scale(0.6);
    opacity: 0;
  }
  60% {
    transform: scale(1.1);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
